import React from "react";

const Resume = props => {
  if (props.data) {
    var skillmessage = props.data.skillmessage;
    var education = props.data.education.map(education => {
      return (
        <div key={education.school}>
          <h3>{education.school}</h3>
          <p className="info">
            {education.degree} <span>&bull;</span>
            <em className="date">{education.graduated}</em>
          </p>
          <p>{education.description}</p>
        </div>
      );
    });

    const headings = {
      fontSize: "30px"
    };

    var work = props.data.work.map(work => {
      return (
        <div key={work.company}>
          <h3>{work.company}</h3>
          <p className="info">
            {work.title}
            <span>&bull;</span> <em className="date">{work.years}</em>
          </p>
          <p>{work.description}</p>
        </div>
      );
    });

    var skills = props.data.skills;
    var skillArr = props.data.skillArr;
    var renderedSkills = [];
    for (var i = 0; i < skillArr.length; i++) {
      renderedSkills[i] = (
        <div className="four columns" key={i}>
          <h3 key={i}>{skillArr[i].toUpperCase()}</h3>
          <ul>
            {skills[skillArr[i]].map((skill, index) => {
              return (
                <li key={index}>
                  <span style={headings}>
                    <i className={skill.image}></i>
                  </span>
                  {"              " + skill.name}
                </li>
              );
            })}
          </ul>
        </div>
      );
    }
  }
  console.log(renderedSkills);
  return (
    <section id="resume">
      <div className="row education">
        <div className="three columns header-col">
          <h1>
            <span>Education</span>
          </h1>
        </div>

        <div className="nine columns main-col">
          <div className="row item">
            <div className="twelve columns">{education}</div>
          </div>
        </div>
      </div>

      <div className="row work">
        <div className="three columns header-col">
          <h1>
            <span>Work</span>
          </h1>
        </div>

        <div className="nine columns main-col">{work}</div>
      </div>

      <div className="row skill">
        <div className="three columns header-col">
          <h1>
            <span>Skills</span>
          </h1>
        </div>

        <div className="nine columns main-col">
          <p className="text-center">{skillmessage}</p>
        </div>
        <div className="row text-center">{renderedSkills}</div>
      </div>
    </section>
  );
};

export default Resume;
